.Templates{
	width: 100vw;
	height: 86vh;
	background-color:  #f9dec9;
    overflow: scroll;
}

.templatesVariety{
/*	height: 84vh;*/
    width: 80vw;
    
    text-align: left;
    padding-bottom: 5vh;
    padding-top: 5vh;
    padding-left: 10vw;
    display: flex;
    column-gap: 2vw;
    flex-wrap: wrap;
    row-gap: 10vh;
    justify-content: center;
}
.template{
    width: 300px;
    height: 400px;
    box-shadow:rgba(0,0,0,0.5) 0px 5px 15px, inset rgba(0,0,0,0.1) 0px -10px 20px;
}
.TempImg{
    width: 300px;
    height: 400px;
}

.use{
    width: 300px;
    height: 40px;
    border: none;
    background-color: #5fbff9;
    border-radius: 5px;
    cursor: pointer;
    font-size: 20px;
    box-shadow:rgba(0,0,0,0.5) 0px 5px 15px, inset rgba(0,0,0,0.1) 0px -10px 20px;
}

.use:hover{
    transition: 1.2s;
    transform: scale(1.2);
}

@media screen and (max-width: 350px){
    .template{
        width: 200px;
        height: 300px;
    }
    .templatesVariety{
        padding-left: 10vw;

    }
    .TempImg{
    width: 200px;
    height: 300px;
    }
    .use{
    width: 200px;
    height: 30px;
    border: none;
    background-color: #5fbff9;
    border-radius: 5px;
    cursor: pointer;
    font-size: 20px;
    box-shadow:rgba(0,0,0,0.5) 0px 5px 15px, inset rgba(0,0,0,0.1) 0px -10px 20px;
}
}
