.Contact{
	width: 100vw;
	height: 86vh;
	overflow: scroll;
	background-color: #f9dec9;
}
.contact{
	padding-top: 2vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	overflow: scroll;
}
.webDev{
	font-family: monospace;
}

.socialIcons{
	display: flex;
	justify-content: center;
	column-gap: 12px;
}

.socialImg{
	height: 33px;
}
.headContact{
	color: black;
	text-decoration: none;

}


.headC{
	margin-bottom: 0px;
	font-family: monospace;
}

.contactForm{
	width: 400px;
	height: 350px;
	border: none;
	padding-top: 2vh;
}

@media screen and (max-width:480px){
	.contactForm{
		width: 350px;
	}
	.socialImg{
		height: 33px;
	}
	.headC{
		font-size: 28px;
	}
	.webDev{
		font-size: 19px;
	}
}

@media screen and (max-width:415px){
	.contactForm{
		width: 300px;	}
}
@media screen and (max-width:385px){
	.contactForm{
		width: 250px;	}
	.socialImg{
		height: 28px;
	}
	.headC{
		font-size: 26px;
	}
	.webDev{
		font-size: 17px;
	}
}
@media screen and (max-width:330px){
	.contactForm{
		width: 200px;	}

}