.Support{
	width: 100vw;
	height: 86vh;
	background-color: #f9dec9;
	overflow: scroll;
	display: flex;
	justify-content: center;
}

#kofiframe{
	height: 80vh;
	width: 380px;
	margin-top: 3vh;
	
	border: none;
	border-radius: 10px;
	box-shadow:rgba(0,0,0,0.5) 0px 5px 15px, inset rgba(0,0,0,0.1) 0px -10px 20px;


}

@media screen and (max-width:400px){
	#kofiframe{
		width: 300px;
	}
}

@media screen and (max-width:300px){
	#kofiframe{
		width: 250px;
	}
}
